import {
    Component,
    h,
    Prop,
    State,
    Element,
    Event,
    EventEmitter,
    Host,
} from '@stencil/core';
import { fetchTranslations } from '../../utils/translation';
import { cva } from 'class-variance-authority';
import { SelectOption } from '../../types/select';
import {
    handleArrowNavigation,
    handleHomeEndNavigation,
} from '../../utils/keyboard-navigation';

const inputVariants = cva(
    'placeholder-text-light transition[background-color,color,box-shadow] relative flex min-h-36 min-h-[36px] w-full flex-shrink flex-grow basis-0 gap-4 rounded-md border border-solid py-4 pr-24 pl-8 duration-300 ease-in-out outline-none focus-within:ring-2 focus-within:outline-0',
    {
        variants: {
            disabled: {
                false: 'focus-within:ring-active-foreground/30 bg-white',
                true: 'bg-surface-1 !text-disabled pointer-events-none border-none',
            },
            readonly: {
                false: 'focus-within:ring-active-foreground/30',
                true: '!bg-surface-1 !text-dark cursor-text border-none',
            },
            invalid: {
                false: 'border-med focus-within:border-active-dark focus-within:ring-active-foreground/30',
                true: 'border-error-base focus-within:border-error-base focus-within:ring-destructive-foreground/30',
            },
            typeahead: {
                false: 'cursor-pointer caret-transparent',
                true: null,
            },
        },
        defaultVariants: {
            disabled: false,
            readonly: false,
            invalid: false,
            typeahead: false,
        },
    },
);

const optionVariants = cva(
    'transition[background-color,color,box-shadow] text-body focus:ring-active-foreground/40 flex w-full cursor-pointer items-center truncate p-8 font-normal duration-300 ease-in-out focus:ring-2 focus:outline-0 focus:ring-inset',
    {
        variants: {
            active: {
                true: 'bg-active-light text-active',
                false: 'hover:bg-disabled-light bg-white',
            },
        },
    },
);

/**
 * @category Form Controls
 * @description A multi-selection dropdown component for choosing multiple values from a list of options. Features search functionality, keyboard navigation, and accessibility support.
 */
@Component({
    tag: 'atui-multi-select',
    shadow: false,
})
export class AtuiMultiSelectComponent {
    /**
     * Options displayed in the dropdown menu.
     */
    @Prop()
    options: SelectOption[];

    /**
     * Label of the select.
     */
    @Prop()
    label?: string = '';

    /**
     * Short description or validation hint if required.
     */
    @Prop()
    hint_text?: string = '';

    /**
     * Error text for the select.
     */
    @Prop()
    error_text?: string = '';

    /**
     * Optional info icon with detailed tooltip description. Displayed at right of label.
     */
    @Prop()
    info_text?: string = '';

    /**
     * Placeholder text for the select.
     */
    @Prop()
    placeholder?: string = '';

    /**
     * Indicated form field is required.
     */
    @Prop()
    required?: boolean;

    /**
     * Set the select to appear invalid.
     */
    @Prop()
    invalid?: boolean;

    /**
     * Set the select to be clearable.
     */
    @Prop()
    clearable?: boolean;

    /**
     * Disable user interaction. Disabled state should be applied via form control.
     */
    @Prop()
    disabled?: boolean;

    /**
     * Set input to readonly mode, allows users to select any active values.
     */
    @Prop()
    readonly?: boolean;

    /**
     * Set the select to enable typeahead search.
     */
    @Prop()
    typeahead?: boolean;

    /**
     * The selected items
     */
    @Prop({ mutable: true })
    value: string[] = [];

    @State() searchText: string = '';
    @State() isOpen: boolean = false;
    @State() translations: any;
    @State() hasMatchingOptions: boolean = false;
    @State() parentWidth: string = '';

    @Element() el: HTMLElement;
    private menuId = `dropdown-${Math.random().toString(36).substring(2, 11)}`;
    private menuRef: HTMLAtuiMenuElement;
    private searchInputEl: HTMLInputElement;

    /**
     * Emits an event containing a list of the selected items when the selection changes.
     */
    @Event()
    atuiChange: EventEmitter<string[]>;

    async componentWillLoad(): Promise<void> {
        this.translations = await fetchTranslations(this.el);
    }

    componentDidLoad() {
        const parentRect = this.el.getBoundingClientRect();
        this.parentWidth = `${parentRect.width}px`;
    }

    private updateIsOpenState(event: CustomEvent<boolean>): void {
        this.isOpen = event.detail;

        if (this.isOpen && this.typeahead) {
            requestAnimationFrame(() => {
                if (this.searchInputEl) {
                    this.searchInputEl.focus();
                }
            });
        }
    }

    private handleChange(option: string) {
        this.value = this.value.includes(option)
            ? this.value.filter((item) => item !== option)
            : [...this.value, option];
        this.atuiChange.emit(this.value);
    }

    handleClear() {
        this.searchText = '';
    }

    private async handleKeyDownMenu(event: KeyboardEvent): Promise<void> {
        if (event.key === 'Enter' || event.key === ' ') {
            if (event.target instanceof HTMLLIElement) {
                event.preventDefault();
                event.target.click();
            }
            return;
        }

        const menuContainer = this.el.querySelector(
            `ul[id="${this.menuId}"]`,
        ) as HTMLElement;
        if (!menuContainer) return;

        handleArrowNavigation(event, menuContainer);
        handleHomeEndNavigation(event, menuContainer);
    }

    private handleSearchInput(event: Event): void {
        const inputEl = event.target as HTMLInputElement;
        this.searchText = inputEl.value.toLowerCase();
        const trimmedSearch = this.searchText.trim().toLowerCase();
        this.hasMatchingOptions = trimmedSearch
            ? this.options?.some((option) =>
                  option.value.toLowerCase().includes(trimmedSearch),
              )
            : true;
    }

    render() {
        return (
            <Host
                class="group/select"
                onFocusout={async (event: FocusEvent) => {
                    const relatedTarget = event.relatedTarget as HTMLElement;
                    if (!relatedTarget || !this.el.contains(relatedTarget)) {
                        this.handleClear();
                        setTimeout(async () => {
                            await this.menuRef?.closeMenu();
                        }, 100);
                    }
                }}
            >
                {this.renderLabel()}
                <atui-menu
                    ref={(el) => (this.menuRef = el)}
                    trigger="click"
                    align="start"
                    width={this.parentWidth}
                    role="listbox"
                    autoclose={false}
                    disabled={this.disabled || this.readonly}
                    onAtuiMenuStateChange={(event) =>
                        this.updateIsOpenState(event)
                    }
                >
                    {this.renderInput()}
                    {!this.disabled || !this.readonly
                        ? this.renderOptions()
                        : null}
                </atui-menu>

                <div>
                    {this.error_text && this.invalid && (
                        <span
                            data-name="multi-select-error"
                            class="text-error"
                        >
                            {this.error_text}
                        </span>
                    )}
                </div>
            </Host>
        );
    }

    renderLabel() {
        return (
            <div class="mb-4 flex flex-col">
                <slot name="label" />
                {(this.label || this.required || this.info_text) && (
                    <atui-form-label
                        for={this.menuId}
                        label={this.label}
                        required={this.required && !this.readonly}
                        info_text={this.info_text}
                    />
                )}
                {this.hint_text && (
                    <span
                        class="text-med text-xs leading-tight"
                        data-name="multi-select-hint"
                    >
                        {this.hint_text}
                    </span>
                )}
            </div>
        );
    }

    renderInput() {
        const classname = inputVariants({
            invalid: this.invalid,
            disabled: this.disabled,
            readonly: this.readonly,
            typeahead: this.typeahead,
        });

        return (
            <div
                class={classname}
                slot="menu-trigger"
                tabindex={0}
            >
                <div>
                    <atui-chip-list
                        size="sm"
                        class="w-full focus-within:ring-0"
                        readonly={this.readonly}
                        disabled={this.disabled}
                        show_clear_all={this.clearable}
                        onAtuiRemoveChip={(event) => {
                            this.value = this.value.filter(
                                (item) => !event.detail.includes(item),
                            );
                            this.atuiChange.emit(this.value);
                        }}
                        chips={this.value}
                        data-name="multi-select-chips-inside"
                    >
                        {this.value.length === 0 && (
                            <div class="text-body text-disabled pointer-events-none select-none">
                                {this.placeholder}
                            </div>
                        )}
                    </atui-chip-list>
                </div>

                {!this.readonly && !this.disabled && (
                    <div class="absolute right-4 flex items-center">
                        <div
                            class="user-select-none text-foreground pointer-events-none flex h-full items-center bg-transparent p-4"
                            role="presentation"
                            tabindex={-1}
                        >
                            <span
                                class="material-icons h-16 w-16 text-[16px] leading-[16px]"
                                data-name="button-icon-right"
                            >
                                {this.isOpen
                                    ? 'arrow_drop_up'
                                    : 'arrow_drop_down'}
                            </span>
                        </div>
                    </div>
                )}
            </div>
        );
    }

    renderOptions() {
        return (
            <ul
                id={this.menuId}
                role="listbox"
                class="contents"
                onKeyDown={async (event) => {
                    await this.handleKeyDownMenu(event);
                }}
            >
                {this.typeahead && (
                    <div class="relative z-10 bg-white p-4">
                        <input
                            data-name="multi-select-input"
                            type="text"
                            class={`transition[background-color,color] bg-surface-1 ring-active-foreground/30 mb-4 h-[28px] w-full flex-shrink flex-grow basis-0 rounded-md p-8 outline-0 duration-300 ease-in-out focus:ring-2 ${
                                this.clearable ? 'pr-24' : ''
                            } `}
                            placeholder={
                                this.translations?.ATUI?.SEARCH || 'Search'
                            }
                            name=""
                            value={this.searchText}
                            onInput={(event) => {
                                event.stopPropagation();
                                this.handleSearchInput(event);
                            }}
                            onClick={(e) => e.stopPropagation()}
                            ref={(el) => (this.searchInputEl = el)}
                        />

                        {this.clearable && this.searchText !== '' && (
                            <div class="absolute top-4 right-4">
                                <atui-button
                                    size="sm"
                                    icon="cancel"
                                    type="secondaryText"
                                    onClick={(event) => {
                                        event.stopPropagation();
                                        this.searchText = '';
                                        this.searchInputEl.value = '';
                                    }}
                                    data-name="multi-select-search-clear"
                                ></atui-button>
                            </div>
                        )}
                    </div>
                )}

                {this.options
                    ?.filter(
                        (option) =>
                            !this.searchText ||
                            option.value
                                .toLowerCase()
                                .includes(this.searchText),
                    )
                    .map((option) => this.renderOption(option))}

                {this.typeahead &&
                    this.searchText &&
                    !this.hasMatchingOptions && (
                        <div
                            data-name="no-results-found"
                            class="text-body text-light w-full bg-white px-16 py-8"
                        >
                            {this.translations?.ATUI?.NO_RESULTS_FOUND ||
                                'No results found'}
                        </div>
                    )}
            </ul>
        );
    }

    renderOption(option: SelectOption) {
        const classname = optionVariants({
            active: this.value.includes(option.value),
        });

        return (
            <li
                role="option"
                data-name="multi-select-option"
                aria-selected={this.value.includes(option.value)}
                tabIndex={0}
                class={classname}
                onClick={() => this.handleChange(option.value)}
            >
                {option.value}
            </li>
        );
    }
}
